.table {
  margin: 20px 0;
}

.table-header {
  display: -ms-flexbox;
  display: flex;
  line-height: 24px;
  font-size: 16px;
  padding: 0 10px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  text-align: left;
}

.table-empty {
  margin: 200px;
  text-align: center;
  font-size: 16px;
}

.table-row {
  display: -ms-flexbox;
  display: flex;
  line-height: 24px;
  white-space: nowrap;
  margin: 10px 0;
  padding: 10px;
  background: #ffffff;
  border: 1px solid #e3e3e3;
}

.table-header > span {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding: 0 5px;
}

.table-row > span {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding: 0 5px;
}

.Post-Delete{
  float: right;
  top: 25px;
  color: red;
  cursor: pointer;
}

.Post-Edit{
  top: 25px;
  color: #CCC;
  cursor: pointer;
}



